export default {
	// 
	GET_MENUDATA: (state, data) => {
    state.menuList = data
	},
	// 
	GET_CHILDRENDATA: (state, data) => {
    state.childrenList = data
	},
	// slideBar
	GET_SLIDEBARNAME: (state, data) => {
    state.nowplacesecond = data
	},
	// 
	GET_FIRSTNAME: (state, data) => {
    state.nowplacefirst = data
    state.parentsname = data
	}
}



